import React from "react";
import style from './style.module.css'
import mockBooks from "../api/mockBooks";
import Comments from "../comments";

const Book = (props) => {
    const book=mockBooks.filter((b)=>b.id===+props.match.params.id)

    const [{title,description,price,author,image_cover}]=book
    return (
        <div className={style.book}>
            <div className={style.book_img}>
                <img src={image_cover} alt=""/>
            </div>
            <div className={style.book_info}>
                <h2 className={style.book_title}>{title}</h2>
                <span className={style.book_author}>
                   автор: {author}
                </span>
                <span className={style.book_price}> Цена: {price} <sub>грн</sub> </span>

                <p className={style.book_description}>{description}</p>
            </div>
            <div className={style.comments}>
                <Comments/>
            </div>

        </div>
    )
}

export default Book