import React from "react";
import style from './style.module.css'
import {Link} from "react-router-dom";


const BookLittle = (props) => {
    return (
        <>
            {props.books.map(({title, description, price, author, image_cover, id}) =>
                <div className={style.bookLittle} key={id}>
                    <div>
                       <Link to={`/book/${id}`}><img src={image_cover} alt=""/></Link>
                    </div>
                    <div className={style.bookLittle__info}>
                        <span className={style.bookLittle__title}>{title}</span>
                        <span>{author}</span>
                        <span>{price} грн</span>
                    </div>
                </div>)}
        </>
    )
}

export default BookLittle