import React from "react";
import style from './style.module.css'
import {Formik, Form, Field, ErrorMessage} from "formik";

const Modal = (props) => {
    return (
        <div className={style.modal}>
            <h3>Добавить новую книгу</h3>
            <Formik
                initialValues={{title: '',description:'',price:'',author:'',image_cover:''}}
                validate={values => {
                    const errors = {};
                    if (!values.title) {
                        errors.title = 'Required';
                    }
                    return errors;
                }}

                onSubmit={(values,errors) => {
                    props.onAddNewBook(values)
                    props.onAddBook(false)
                }}>
                    <Form>
                        <div>
                            <Field as='input' type="text" name='title'placeholder='Название '
                            />
                            <ErrorMessage name="title"/>
                        </div>
                        <div>
                            <Field as='input' type="text" name='description' placeholder='Описание'/>
                            <ErrorMessage name="title"/>
                        </div>
                        <div>
                            <Field as='input' type="text" name='price' placeholder='Цена'/>
                            <ErrorMessage name="title"/>
                        </div>
                        <div>
                            <Field as='input' type="text" name='author' placeholder='Автор'/>
                            <ErrorMessage name="title"/>
                        </div>
                        <div>
                            <Field as='input' type="text" name='image_cover' placeholder='Обложка'/>
                            <ErrorMessage name="title"/>
                        </div>

                        <button type={"submit"}>Submit</button>
                    </Form>
            </Formik>
        </div>
    )
}

export default Modal