import React, {useState} from "react";
import style from './style.module.css'


const Comments = () => {

    const [currentValue, changeCurrentValue] = useState('');
    const [comments, newComment] = useState([]);



    const addComment = () => {
        newComment([
            ...comments,
            {text: currentValue, id: Date.now()},
        ])
        changeCurrentValue('')
    };
    const emptyComment = comments.length === 0 ? <div className={style.emptyComment}>Здесь еще никто ничего не писал</div> : null
    return (
        <div className={style.comments}>
            <h3 className={style.commentsTitle}>Оставить комментарий</h3>
            <input
                value={currentValue} onChange={(e) => changeCurrentValue(e.target.value)}
                className={style.commentsInput} type="text"/>
            <button
                onClick={addComment}
                className={style.commentsBtn}
            >Комментировать</button>
            <div className={style.comment}>
                {emptyComment}
                {comments.map(({text, id}) => (
                    <div className={style.commentOne} key={id}>
                        <div className={style.commentText}>{text}</div>
                    </div>

                ))}
            </div>

        </div>
    )
};

export default Comments