import React, {useState, useReducer} from "react";
import BookLittle from "./bookLittle";
import style from './style.module.css'
import BookListReducer from '../reducers/bookList'
import Modal from "../modal";
import mockBooks from "../api/mockBooks";


const BookList = () => {
    const [addBook, onAddBook] = useState(false);
    const [state, dispatch] = useReducer(BookListReducer, [...mockBooks])


    const onAddNewBook = ({title,description,price,author,image_cover},props) => {
        debugger
        dispatch({
            type: 'ADD_NEW_BOOK',
            payload: {
                title: title,
                description: description,
                price: price,
                author: author,
                image_cover: '/images/terror.jpg',
                id: 1000
            }
        })

    }

        const modal = addBook ? <div className={style.modal}><Modal onAddBook={onAddBook} onAddNewBook={onAddNewBook}/></div> : null;
    return (
        <div className={style.bookList}>
            <div className={style.bookListHeader}>
                <h2 className={style.bookTittle}>Список книг</h2>
                <h2 onClick={() => onAddBook(true)} className={style.addBook}>Добавить книгу</h2>
            </div>
            {modal}
            <div className={style.bookList__books}>
                <BookLittle onAddNewBook={onAddNewBook} books={state}/>
            </div>

        </div>
    );
}

export default BookList